.App {
  text-align: center;
}

.BotFrame {
  text-align: right;
}


.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  color: white;
}
.nav-bar {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  padding: 0;
}
.Container {
  background-color: #222;
  height: 50px;
  padding: 20px;
  position: relative;
  width: 100%;
  display: block;
}
.mbr-table {
  display: table;
  width: 100%;
}
.link,.dropdown-item {
  color: #ffffff;
}
.link{
  font-size: 0.75rem;
}
.mbr-table-cell {
  display: table-cell;
  float: none;
  padding-bottom: 0;
  padding-top: 0;
  position: relative;
  vertical-align: middle;
  height: 3.625rem;
}
.nav-dropdown {
  display: table !important;
  font-family: "Montserrat";
  font-size: 0.75rem;
  font-weight: 700;
  height: auto !important;
}
.nav-dropdown .nav-item {
  display: table-cell;
  float: none;
  vertical-align: middle;
}
.nav-dropdown .nav-btn {
  padding-left: 1rem;
}

.nav-dropdown .btn {
  font-size: 0.75rem;
  font-weight: 700;
  letter-spacing: 0;
  margin-bottom: 0;
  padding-left: 1.25rem;
  padding-right: 1.25rem;
}
.nav-dropdown .link {
  margin: 1.667em;
  padding: 0;
  -webkit-transition: color .2s ease-in-out;
  -o-transition: color .2s ease-in-out;
  transition: color .2s ease-in-out;
}
.pull-xs-right {
  float: right!important;
}

.navbar-toggler {
  color: #ffffff;
  position: relative;
  padding: .5rem .75rem;
  font-size: 1.25rem;
  line-height: 1;
  background: 0 0;
  border: 1px solid transparent;
  border-radius: 1.4rem;
}
.App-title {
  font-size: 1.5em;
  text-align: left;
}

.App-intro {
  font-size: large;
}
.App-Body {
  position: relative;
  display: block;
}
button, select {
  text-transform: none;
  overflow: visible;
}


button, html input[type=button], input[type=reset], input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer;
}
@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



